.itm_newyear header {
  margin: 0 auto;
}

.itm_newyear header .mobile-menu-btn {
  display: none;
}

.itm_newyear header.header {
  padding-bottom: 0;
}

.itm_newyear header.header .header__main-wrap {
  margin-bottom: 0;
  background-image: url(../images/itmarathon/xmas-img/header-mob.jpg);
}

@media screen and (min-width: 385px) {
  .itm_newyear header.header .header__main-wrap {
    background-image: url(../images/itmarathon/xmas-img/header-tab.jpg);
  }
}

@media screen and (min-width: 768px) {
  .itm_newyear header.header .header__main-wrap {
    background-image: url(../images/itmarathon/xmas-img/header-tab-2x.jpg);
  }
}

@media screen and (min-width: 1280px) {
  .itm_newyear header.header .header__main-wrap {
    background-image: url(../images/itmarathon/xmas-img/header-desk-2x.jpg);
  }
}

.itm_newyear header.header .hd__btn {
  background-color: #19A1CC;
}

.itm_newyear header.header .hd__date,
.itm_newyear header.header .hd__title,
.itm_newyear header.header .hd__subtitle,
.itm_newyear header.header .hd__list {
  color: #fff;
}

.itm_newyear header.header .hd__title span {
  color: #D10000;
  background: url(../images/itmarathon/xmas-img/underline.png) center no-repeat;
}

@media screen and (min-width: 768px) {
  .itm_newyear header.header .hd__title span {
    color: #fff;
    background: none;
  }
}

.itm_newyear header.header .navigation .menu .menu-item a {
  color: #fff;
}

.itm_newyear__expect {
  padding: 0;
}

.itm_newyear__expect ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

.itm_newyear__expect .expect_wrapper {
  padding-top: 28px;
  padding-bottom: 114px;
  background-color: #F7F6F4;
  position: relative;
  width: 100%;
  background-image: url(../images/itmarathon/xmas-img/garland-mob.svg);
  background-position: bottom 0 left 0;
  background-size: 75px;
  background-repeat: no-repeat;
}

@media screen and (min-width: 768px) {
  .itm_newyear__expect .expect_wrapper {
    padding: 55px 0;
  }
}

@media screen and (min-width: 1280px) {
  .itm_newyear__expect .expect_wrapper {
    margin: 0 auto;
    height: 1407px;
    background-image: url(../images/itmarathon/xmas-img/expect-desk.svg), url(../images/itmarathon/xmas-img/garland.svg);
    background-position: top 0 center, bottom -20px left 0;
    background-size: cover, 307px;
  }
}

.itm_newyear__expect .expect_wrapper::after {
  width: 107px;
  position: absolute;
  height: 222px;
  content: "";
  display: block;
  bottom: 0;
  right: 0;
  z-index: 3;
  background-image: url(../images/itmarathon/xmas-img/hat-man-mob-2x.png);
  background-repeat: no-repeat;
  background-size: contain;
}

@media screen and (min-width: 1280px) {
  .itm_newyear__expect .expect_wrapper::after {
    bottom: -20px;
    right: 0;
    width: 296px;
    height: 617px;
    background-image: url(../images/itmarathon/xmas-img/hat-man-desk-2x.png);
  }
}

.itm_newyear__expect .container {
  background-image: url(../images/itmarathon/xmas-img/mob-bg-expect-short.png);
  background-position: top 65px left 0;
  background-repeat: no-repeat;
}

@media screen and (min-width: 1280px) {
  .itm_newyear__expect .container {
    background-image: url(../images/itmarathon/xmas-img/expect-clear-bg.png);
    background-position: top -60px center;
    background-size: 100%;
  }
}

.itm_newyear__expect .expect_question {
  font-size: 24px;
  font-weight: 700;
  text-align: center;
  margin-bottom: 44px;
}

@media screen and (min-width: 768px) {
  .itm_newyear__expect .expect_question {
    margin-bottom: 50px;
    font-size: 40px;
  }
}

.itm_newyear__expect .expect_days-list {
  width: 100%;
  display: flex;
  flex-direction: column;
  gap: 32px;
  margin-bottom: 51px;
  padding-left: 110px;
  background-repeat: no-repeat;
  background-position: top 20px left -40px;
  background-size: 44px 62px;
}

@media screen and (min-width: 768px) {
  .itm_newyear__expect .expect_days-list {
    width: 568px;
    padding: 0;
    margin: 0 auto;
    margin-bottom: 50px;
  }
}

@media screen and (min-width: 1280px) {
  .itm_newyear__expect .expect_days-list {
    margin-bottom: 150px;
    gap: 29px;
    width: 720px;
  }
}

.itm_newyear__expect .expect_days-item {
  position: relative;
  width: 100%;
}

@media screen and (min-width: 768px) {
  .itm_newyear__expect .expect_days-item {
    max-width: 390px;
  }
}

@media screen and (min-width: 1280px) {
  .itm_newyear__expect .expect_days-item {
    height: 120px;
    max-width: 590px;
  }
}

.itm_newyear__expect .expect_days-item > h3 {
  margin-bottom: 4px;
  font-size: 20px;
  text-transform: uppercase;
  line-height: 1.2;
  font-weight: 700;
}

@media screen and (min-width: 768px) {
  .itm_newyear__expect .expect_days-item > h3 {
    margin-bottom: 15px;
  }
}

@media screen and (min-width: 1280px) {
  .itm_newyear__expect .expect_days-item > h3 {
    font-size: 32px;
    line-height: 1.22;
    margin-bottom: 13px;
  }
}

.itm_newyear__expect .expect_days-item > p {
  font-size: 16px;
  line-height: 1.19;
}

@media screen and (min-width: 1280px) {
  .itm_newyear__expect .expect_days-item > p {
    font-size: 24px;
    line-height: 1.21;
  }
}

@media screen and (min-width: 768px) {
  .itm_newyear__expect .expect_days-item:nth-child(odd) {
    text-align: start;
    align-self: flex-start;
  }
}

@media screen and (min-width: 768px) {
  .itm_newyear__expect .expect_days-item:nth-child(even) {
    text-align: end;
    align-self: flex-end;
  }
}

@media screen and (min-width: 1280px) {
  .itm_newyear__expect .expect_days-item:last-child {
    margin-top: -40px;
  }
}

.itm_newyear__expect .expect_suggestion {
  font-size: 24px;
  margin-bottom: 26px;
  font-weight: 700;
  text-align: center;
  display: block;
  max-width: 290px;
  margin: 0 auto;
}

@media screen and (min-width: 768px) {
  .itm_newyear__expect .expect_suggestion {
    margin-bottom: 40px;
  }
}

@media screen and (min-width: 1280px) {
  .itm_newyear__expect .expect_suggestion {
    font-size: 48px;
    margin-bottom: 22px;
    max-width: 694px;
  }
}

.itm_newyear__expect .expect_registration-btn {
  color: #fff;
  position: relative;
  z-index: 3;
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 296px;
  height: 72px;
  font-weight: 700;
  font-size: 20px;
  text-transform: uppercase;
  background-color: #19A1CC;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  border: 0;
  margin: 0 auto;
  transition: box-shadow 200ms;
}

.itm_newyear__expect .expect_registration-btn:hover,
.itm_newyear__expect .expect_registration-btn:focus {
  box-shadow: 0px 4px 30px rgba(254, 200, 48, 0.45);
}

.itm_newyear__expect .expect_cloud-text {
  display: none;
}

@media screen and (min-width: 1280px) {
  .itm_newyear__expect .expect_cloud-text {
    position: absolute;
    bottom: 25%;
    left: 60%;
    display: block;
    width: 317px;
    height: 332px;
    background-image: url(../images/itmarathon/xmas-img/cloud-think.svg);
    background-repeat: no-repeat;
    background-size: cover;
  }
}

.itm_newyear__expect .expect_cloud-text > p {
  top: 50%;
  left: 50%;
  transform: translate(-50%, -55%);
  width: 160px;
  position: absolute;
  text-align: center;
  font-size: 15px;
  font-weight: 700;
  line-height: 1.4;
}

.itm_newyear__expect .expect_cloud-text > p > span {
  text-transform: uppercase;
}